<template>
  <div class="message-input">
    <VaInput
      v-model="message"
      @keyup.enter="sendMessage"
      preset="bordered"
      label="Type your message"
    />
    <VaButton @click="sendMessage">Send</VaButton>
  </div>
</template>

<script>
export default {
  name: 'MessageInput',
  data() {
    return {
      message: ''
    };
  },
  methods: {
    sendMessage() {
      if (this.message.trim() !== '') {
        this.$store.dispatch('addMessage', this.message); // Vuex„Çπ„Éà„Ç¢„ÅÆ„Ç¢„ÇØ„Ç∑„Éß„É≥„ÇíÂëº„Å≥Âá∫„Åô
        // „É°„ÉÉ„Çª„Éº„Ç∏„Çí„ÇØ„É™„Ç¢
        this.message = '';
      }
    }
  }
};
</script>

